body {
    font-family: 'Arial', sans-serif;
    background-color: #f5f5f5;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    height: 100vh;
}

.App {
    text-align: center;
    margin-top: 50px;
}

h1 {
    color: #333;
    font-weight: 700;
}

.title {
    margin-bottom: 20px;
}


.squares {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.square {
    width: 3rem;
    height: 3rem;
    margin: 0 0.2rem;
    perspective: 1000px;
}

.square-inner {
    position: relative;
    width: 100%;
    height: 100%;
    transition: transform 0.8s;
    transform-style: preserve-3d;
}

.square.correct .square-inner,
.square.incorrect .square-inner {
    transform: rotateY(180deg);
}

.square.correct .square-inner {
    transition-delay: 0s;
}

.square.correct:nth-child(2) .square-inner {
    transition-delay: 0.3s;
}

.square.correct:nth-child(3) .square-inner {
    transition-delay: 0.6s;
}

.square.correct:nth-child(4) .square-inner {
    transition-delay: 0.9s;
}

.square.correct:nth-child(5) .square-inner {
    transition-delay: 1.2s;
}

.square.incorrect .square-inner {
    transition-delay: 0s;
}

.square.incorrect:nth-child(2) .square-inner {
    transition-delay: 0.3s;
}

.square.incorrect:nth-child(3) .square-inner {
    transition-delay: 0.6s;
}

.square.incorrect:nth-child(4) .square-inner {
    transition-delay: 0.9s;
}

.square.incorrect:nth-child(5) .square-inner {
    transition-delay: 1.2s;
}

.square-front,
.square-back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #000;
    font-size: 1.5rem;
}

.square-back {
    transform: rotateY(180deg);
}

.correct .square-back {
    background-color: green;
    color: white;
}

.incorrect .square-back {
    background-color: gray;
    color: white;
}

p {
    font-size: 1.2rem;
    margin-top: 20px;
}

.kana-buttons {
    margin-top: 20px;
    display: grid;
    grid-template-columns: repeat(5, 1fr) repeat(5, 1fr);
    gap: 0.2rem;
    justify-items: center;
}

    .kana-buttons button {
        margin: 2px; /* ÔøΩ{ÔøΩ^ÔøΩÔøΩÔøΩÃÉ}ÔøΩ[ÔøΩWÔøΩÔøΩÔøΩí≤êÔøΩ */
        padding: 12px 7px; /* ÔøΩ{ÔøΩ^ÔøΩÔøΩÔøΩÃÉpÔøΩfÔøΩBÔøΩÔøΩÔøΩOÔøΩí≤êÔøΩ */
        border: 1px solid #000;
        border-radius: 5px;
        background-color: transparent;
        color: #000;
        font-size: 14px;
    }

.submit-button {
    display: inline-block;
    margin: 0px 10px;
    padding: 5px 20px;
    font-size: 16px;
    font-weight: bold;
    text-decoration: none;
    color: #fff;
    background-color: #000000;
    border-radius: 5px;
    transition: background-color 0.3s ease;
}

.clear-button {
    display: inline-block;
    margin: 0px 10px;
    padding: 5px 10px;
    font-size: 14px;
    text-decoration: none;
    color: #000;
    border-radius: 5px;
    background-color: #fff;
    transition: background-color 0.3s ease;
}

.history {
    margin-top: 20px;
    margin-bottom: 20px;
}

    .history h2 {
        font-size: 1.5rem;
    }

.history-entry {
    display: flex;
    justify-content: center;
    margin: 5px 0;
}

.clear-message {
    margin-top: 10px;
    font-size: 30px;
}


/* overlay */

.rule-input {
    display: none;
}

.rule-text {
    margin-bottom: 20px;
    font-size: 16px;
}


/* ÔøΩ|ÔøΩbÔøΩvÔøΩAÔøΩbÔøΩvwindowÔøΩÔøΩÔøΩÔøΩ */
#overlay {
    visibility: visible;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 70;
    width: 100%;
    height: 100%;
}
/* ÔøΩIÔøΩ[ÔøΩoÔøΩ[ÔøΩÔøΩÔøΩCÔøΩÃîwÔøΩiÔøΩÔøΩÔøΩÔøΩ */
#bg_gray {
    background: rgba(0,0,0,0.5);
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 80;
}
/* ÔøΩEÔøΩBÔøΩÔøΩÔøΩhÔøΩEÔøΩÔøΩÔøΩÔøΩ */
#window {
    min-height: 200px;
    width: 70%;
    padding: 20px;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    background: #fff;
    border-radius: 10px;
    box-shadow: 0px 0px 20px -6px rgba(0,0,0,0.6);
    z-index: 90;
    opacity: 1;
}
/* ÔøΩ¬ÇÔøΩÔøΩÔøΩ{ÔøΩ^ÔøΩÔøΩ */
#btn_cloth {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: transparent;
    border-radius: 5px;
    z-index: 100;
    cursor: pointer;
}

    #btn_cloth:hover {
        opacity: 0.7;
    }

    #btn_cloth span,
    #btn_cloth span::before {
        display: block;
        height: 3px;
        width: 25px;
        border-radius: 3px;
        background: #333;
    }

    #btn_cloth span {
        transform: rotate(45deg);
    }

        #btn_cloth span::before {
            content: "";
            position: absolute;
            bottom: 0;
            transform: rotate(-90deg);
        }


/* ÔøΩNÔøΩÔøΩÔøΩbÔøΩNÔøΩ≈ï\ÔøΩÔøΩ */
#popup:checked ~ #overlay {
    visibility: hidden;
}

    #popup:checked ~ #overlay #window {
        animation: fadein 500ms forwards;
        animation-timing-function: ease-in-out;
    }

@keyframes fadein {
    100% {
        opacity: 0;
    }
}

#txt_label {
    text-decoration: underline;
    cursor: pointer;
}

    #txt_label:hover {
        opacity: 0.7;
    }
